import { useEffect, useState } from "react";
import { BiChevronsRight } from "react-icons/bi";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

function App() {
  const URL = "https://course-api.com/react-tabs-project";

  const [loading, setLoading] = useState(true);
  const [tabIndex, setTabIndex] = useState(0);
  const [tabData, setTabData] = useState([]);

  const fetchData = async () => {
    setLoading(true);
    const response = await fetch(URL);
    const data = await response.json();
    setTabData(data);
    setLoading(false);
  };

  useEffect(() => {
    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="loading-sec">
        <h1>Loading...</h1>
      </div>
    );
  }
  const { title, dates, company, duties } = tabData[tabIndex];

  return (
    <div className="App">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="heading-section text-center">
              <h1>Job Description</h1>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-3">
            <ul className="list-unstyled button-list">
              {tabData.map((item, index) => {
                return (
                  <li>
                    <button
                      className={index === tabIndex ? "active" : null}
                      key={index}
                      onClick={() => setTabIndex(index)}
                    >
                      {item.company}
                    </button>
                  </li>
                );
              })}
            </ul>
          </div>
          <div className="col-md-9">
            <div className="text-contents">
              <div className="top-info">
                <h4 class="job-title">{title}</h4>
                <h5 className="company">Company: {company}</h5>
                <h5 className="dates">{dates}</h5>
              </div>
              <ul className="list-unstyled">
                {duties.map((item) => {
                  return (
                    <li>
                      <BiChevronsRight />
                      {item}
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
